$nav-width: 200px;

//The sidenav
#sidebar {
  width: $nav-width;
  left: -$nav-width;
  transition: transform 0.5s ease-in-out;
}

//When sidenav is open:
//a. Create a transparent overlay over the content
.sidenav-active .cover {
  background-color: rgba(0, 0, 0, 0.4);
  transition: background-color 0.5s ease-in-out;
}

//b. SlideIn everything to the side
#sidebar,
.menu-btn {
  transition: transform 0.5s ease-in-out;
  .sidenav-active & {
    transform: translateX($nav-width);
  }
}

//c. Prevent body scroll
.noscroll {
  position: relative; //for ios
  overflow: hidden;
}

// Menu animation
.menu-btn {
  width: 60px;
  height: 60px;
  transition: all 0.5s ease-in-out;
}
.menu-btn__burger {
  width: 30px;
  height: 4px;
  background: #4677e6;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(56, 66, 199, 0.2);
  transition: all 0.5s ease-in-out;
}
.menu-btn__burger::before,
.menu-btn__burger::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 4px;
  background: #3e46cc;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(56, 66, 199, 0.2);
  transition: all 0.5s ease-in-out;
}
.menu-btn__burger::before {
  transform: translateY(-12px);
}
.menu-btn__burger::after {
  transform: translateY(12px);
}
/* ANIMATION */
.menu-btn.open .menu-btn__burger {
  transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}
.menu-btn.open .menu-btn__burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn__burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}
