@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap");
// @import url("https://fonts.googleapis.com/css2?family=Varela+Round&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap");

$bezier-transition: transform 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0s,
  box-shadow 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0s;

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "Open Sans", sans-serif;
  color: #304564;
  transition: all 0.2s ease-in-out !important;
  transition-delay: 0s !important;
  scroll-behavior: smooth;
}

.logo-color {
  color: #3e46cc;
}
.dark-text {
  color: #2f3e54;
}
.blue-text {
  // color: #336ff5;
  color: #4777e6;
}
.light-blue-text {
  color: #ccd5e8;
}
.blue-bg {
  background: #4777e6;
}
.blue-gr-bg {
  // background: linear-gradient(to right, #2b32b2, #1488cc);
  background: linear-gradient(to right, #2d56b3, #407bff);
  // background: #00000015;
}

.icon-bg {
  background: rgba(71, 119, 230, 0.5);
  padding: 0.3rem;
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
}

.hr-left-heading:before,
.hr-right-heading:after {
  content: "";
  flex: 1 1;
  border-bottom: 2px solid #5b8cf7;
  margin: auto 0.5rem;
  width: 3rem;
}

.highlight {
  background: rgba(51, 111, 245, 0.8);
  color: #fff;
  padding: 0 3px;
}

.text-4xxl {
  font-size: 2.5rem;
}

.text-xxxl {
  font-size: 10rem;
}

.text-6xxl {
  font-size: 6rem;
}

.opacity-10 {
  opacity: 0.1;
}

// Gradient Button
.blue-gr-btn {
  padding: 0.7rem 1rem;
  color: #fff;
  align-items: center;
  background: #00d2ff;
  background: linear-gradient(to right, #336df1, #7ea4f3);
  transition: transform 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0s,
    box-shadow 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0s,
    background-position 0.4s ease-in;

  background-image: linear-gradient(
    to right,
    #336df1,
    #799eee,
    #4071e4,
    #1a59e0
  );
  box-shadow: 0 4px 15px 0 rgba(65, 132, 234, 0.75);
  background-size: 300% 100%;

  &:hover {
    background-position: 100% 0;
    transform: scale(1.1);
    // box-shadow: 0 10px 15px -3px rgba(64, 123, 255, 0.2),
    //   0 4px 6px -2px rgba(64, 123, 255, 0.04);
  }
}

.blue-br-btn {
  padding: 0.6rem 1rem;
  align-items: center;
  border: 2px solid #3971f1;
  color: #3971f1;
  transition: $bezier-transition;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  svg {
    transition: $bezier-transition;
  }
  &:hover {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
      0 4px 6px -2px rgba(0, 0, 0, 0.05);
    transform: scale(1.1);
    svg {
      transform: translate3d(10px, 0, 0);
    }
  }
}

/* Hero :START */
.hero-section {
  height: calc(100vh + 5.5rem);
}
// .hero-bg {
//   background-image: url(../img/hero-bg.svg);
//   background-position: left;
//   background-size: cover;
// }
/* Hero :END */

/* Brands :START */
#brands {
  top: -10vh;
  .brands-container img {
    filter: grayscale(1);
    transition: all 0.2s ease;
    &:hover {
      filter: grayscale(0);
    }
  }
}
/* Brands :END */

.hover-card {
  transition: box-shadow 0.3s ease-in-out;
  &:hover {
    box-shadow: 25px 25px 50px 0 rgba(0, 0, 0, 0.15);
  }
}

.printer-bg {
  background-image: url(../img/printer.png);
  background-position: left;
  background-size: cover;
}

#contactCard {
  bottom: 10px;
  right: 10px;
  border: 5px solid #4a5f92;
  color: #4a5f92;
  height: 0;
  opacity: 0;
  transition: height 0.3s ease-out, opacity 0.3s ease-in;
}

#goUp {
  bottom: 160px;
  right: 0px;
  transition: height 0.3s ease-out, opacity 0.3s ease-in;
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

[data-scroll] {
  transition: all 1s;
}

[data-scroll="in"],
.delay-scroll1[data-scroll="in"],
.delay-scroll2[data-scroll="in"],
.delay-scroll3[data-scroll="in"],
.delay-scroll4[data-scroll="in"],
.delay-scroll5[data-scroll="in"] {
  opacity: 1;
  transform: translateY(0);
}
[data-scroll="out"],
.delay-scroll1[data-scroll="out"],
.delay-scroll2[data-scroll="out"],
.delay-scroll3[data-scroll="out"],
.delay-scroll4[data-scroll="out"],
.delay-scroll5[data-scroll="out"] {
  opacity: 0;
  transform: translateY(50px);
}

[data-scroll="in"],
[data-scroll="out"] {
  transition-delay: 0.1s;
}
.delay-scroll1[data-scroll="in"],
.delay-scroll1[data-scroll="out"] {
  transition-delay: 0.2s;
}
.delay-scroll2[data-scroll="in"],
.delay-scroll2[data-scroll="out"] {
  transition-delay: 0.3s;
}
.delay-scroll3[data-scroll="in"],
.delay-scroll3[data-scroll="out"] {
  transition-delay: 0.4s;
}
.delay-scroll4[data-scroll="in"],
.delay-scroll4[data-scroll="out"] {
  transition-delay: 0.5s;
}
.delay-scroll5[data-scroll="in"],
.delay-scroll5[data-scroll="out"] {
  transition-delay: 0.6s;
}
