html:not(.ie-10):not(.ie-11):not(.ie-edge) .wave {
  height: auto;
}
/* Wave light */
.wave-light {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: auto;
  transform: translateY(-50%);
  pointer-events: none;
  z-index: 0;
}

.wave-light .wave-path {
  fill: rgba(77, 68, 104, 0.03);
  animation: wave4 4s ease-in-out infinite;
  transform-origin: 50%;
}

.wave-light.wave-light-blue .wave-path {
  fill: rgba(15, 56, 148, 0.1);
  animation: wave4 5s ease-in-out infinite;
  transform-origin: 50%;
}

.wave-light .wave-path:nth-child(2) {
  animation-delay: 2s;
}

.wave-light .wave-path:nth-child(3) {
  animation-delay: 4s;
}

.wave-light-reverse-x {
  transform: translateY(-50%) scaleX(-1);
}

.wave-light-reverse-y {
  transform: translateY(-50%) scaleY(-1);
}

.wave-light-1 {
  top: 53%;
}

.wave-light-2 {
  top: 43%;
}

.wave-light-3 {
  top: 57.5%;
}

.wave-light-4 {
  top: 91.5%;
}

.wave-light-5 {
  top: 47%;
}

.wave-light-6 {
  top: 80%;
}

.wave-light-7 {
  top: 86%;
}

.wave-light-8 {
  top: 78%;
}

.wave-light-9 {
  top: 63%;
}

.wave-light-10 {
  top: 0;
}

.wave-light-11 {
  top: 10%;
}

.wave-light-12 {
  top: 74%;
}

.wave-light-13 {
  top: 100%;
}

.wave-light-14 {
  top: 20%;
}

@media (min-width: 576px) {
  .wave-light-13 {
    top: 106%;
  }
  .wave-light-14 {
    top: 27%;
  }
}

@media (min-width: 992px) {
  .wave-light-13 {
    top: 108%;
  }
}

@keyframes wave4 {
  25% {
    transform: scaleY(0.9);
  }
  75% {
    transform: scaleY(1.1);
  }
}
/* wave-primary */
.wave-primary {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: auto;
  pointer-events: none;
}

.wave-primary .wave-path {
  transform: scale(1.1, 1);
  transform-origin: 50% 100%;
}

.wave-primary .wave-path-1 {
  // fill: rgba(0, 242, 169, 0.3);
  // fill: rgba(117, 159, 248, 0.3);
  fill: rgba(64, 123, 255, 0.35);
  animation: wave5 15s ease-in-out infinite;
}

.wave-primary .wave-path-2 {
  // fill: rgba(0, 242, 169, 0.15);
  // fill: rgba(117, 159, 248, 0.15);
  fill: rgba(64, 123, 255, 0.2);

  animation: wave6 15s ease-in-out infinite;
}

.wave-primary .wave-path-3 {
  // fill: rgba(0, 242, 169, 0.1);
  // fill: rgba(117, 159, 248, 0.1);
  fill: rgba(64, 123, 255, 0.15);

  animation: wave7 15s ease-in-out infinite;
}

.wave-primary-1 {
  overflow: visible;
  width: 61.72%;
}

@keyframes wave5 {
  25% {
    transform-origin: 0 100%;
    transform: scale(1.3, 1.1);
  }
  75% {
    transform: scale(1.15, 0.9);
    transform-origin: 100% 100%;
  }
}

@keyframes wave6 {
  25% {
    transform-origin: 100% 100%;
  }
  50% {
    transform: scale(1.2, 0.8);
  }
  75% {
    transform-origin: 0 100%;
  }
}

@keyframes wave7 {
  25% {
    transform-origin: 0 100%;
  }
  50% {
    transform: scale(1.1, 0.9);
  }
  75% {
    transform-origin: 100% 100%;
  }
}

